import { SectionAndBlogList } from './../models/section-blog-list';
import { ApiServiceService } from './../service/api-service.service';
import { CONSTANTS } from './../constants/constants';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  /* readingContent: any = CONSTANTS.READINGCONTENT
  questionsContent: any = CONSTANTS.QUESTIONSCONTENT */
  sectionAndBlogList: Object = {}

  constructor(private apiService: ApiServiceService) {
    this.getBlogList()
   }

  ngOnInit() {
  }

  getBlogList() {
    this.apiService.getBlogList().subscribe((result: SectionAndBlogList) => {
      this.sectionAndBlogList = result
    }, (error) => {
      console.log("error in get blog list api", error)
    })
  }

  updateBlog(id, checkmark) {
    this.apiService.updateBlog(id, !checkmark).subscribe((result: SectionAndBlogList) => {
      this.getBlogList()
    }, (error) => {
      console.log("error in update blog", error)
    })
  }


}
