import { SectionAndBlogList } from './../models/section-blog-list';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiServiceService {

  baseUrl: string = 'https://testtask12.herokuapp.com/'

  constructor(private http: HttpClient) { 
  }

  getBlogList() {
    return this.http.get<SectionAndBlogList>(this.baseUrl + 'blogs/list')
  }

  updateBlog(blogId: string, checkMark: boolean) {
    return this.http.put(this.baseUrl + 'blogs/update/' + blogId, {
      checkmark: checkMark
    })
  }
}
